<template>
    <div class="myCollect">
        <div class="Tops"></div>
        <van-nav-bar
            title="我的收藏"
            left-arrow
            @click-left="goBack"
            />

        <div class="collBodys" v-if="isData">
            <div class="center" v-for="(item,index) in datas" :key="index">
                <img :src="item.smallImg" @click="ff_goPages('Details',item.pid)">
                <p>{{item.name}}</p>
                <p> 
                    <span>{{item.price}}</span>
                    <span class="iconfont icon-lajitong" @click="ff_rovem(item.pid)"></span>
                </p>
            </div>
        </div>
        <div v-if="!isData" class="isData"></div>
       
        
    </div>
</template>

<script>
    import '../assets/font/iconfont.css'
    export default {
        name:'Collect',
        data(){
            return{
                datas  : [],
                isData : true
            }
        },
        methods: {
            goBack(){
                this.$router.go(-1);
            },
            // 跳转模块
            ff_goPages(name,pid=null){
                this.$router.push({name,query:{pid}});
            },
            ff_created(){
                this.axios({
                    url:'/findAllLike',
                    method:'GET',
                    params:{
                        appkey      : this.appkey,
                        tokenString : this.useId,
                    }
                })
                .then(res=>{
                    if(res.data.code == 2000){
                        this.datas = res.data.result;
                        if(this.datas == 0){
                              this.isData = false;
                        }
                    }
                })
                .catch(err=>{
                     
                })
            },
            // 删除删除
            ff_rovem(pid){
                 this.$dialog.confirm({
                    message: '确认删除收藏',
                    })
                    .then(() => {

                        this.axios({
                            url:'/notlike',
                            method:'POST',
                            data:{
                                    appkey: this.appkey,
                                    pid,
                                    tokenString: this.useId
                            }
                        })
                        .then(res=>{
                             
                            if(res.data.code == 900){
                                this.$toast(res.data.msg);
                                this.ff_created(); 
                            }
                        })
                        .catch(err=>{
                             
                        })

                        
                            
                    })
                    .catch(()=>{
                         
                    })
            }
        },
        created() {
             this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
            });



            this.ff_created(); 
        },
    }
</script>

<style lang="less" scoped>
    .myCollect{
        background-color: #f5f5f5;
        min-height: 100%;


        .Tops{
                width: 100%;
                height: 200px;
                background: url('../assets/img/home_page_top_bg.9.png') no-repeat 0px 101%;
                background-size: 100%;
            }

         /deep/.van-nav-bar{
            width: 100%;
            height: 35px;
            position: absolute;
            top: 0px;
            background-color:#f5f5f5;

            /deep/.van-nav-bar__title{
                font-size: 15px;
                font-weight: 540;
            }
            /deep/.van-icon{
                font-size: 20px;
                color: #000;
                font-weight: 550;
            }
            }
        .collBodys{
            width: 95%;
            border-radius: 15px;
            padding: 10px;
            box-sizing: border-box;
            display: flex;
            flex-wrap: wrap;
            justify-content: left;
            background: #fff;
            margin: 0px auto;
            margin-top: -90px;


        }
        .center{
            background: #fff;
            flex: 0 0 43%;
            margin:20px 10px;
            margin-bottom: 5px;

            img{
                width: 100%;
                border-radius: 10px;
            }
            p{
                margin: 0px;
                text-align: left;
            }
            p:nth-child(2){
                font-size: 15px;
                font-weight: 550;
            }
            p:nth-child(3){
                margin: 5px 0px;

                span:nth-child(1){
                    font-size: 13px;
                    font-weight: 550;
                    transform: scale(.9);
                    text-align: left;
                    color: #363c91;
                }
                 span:nth-child(2){
                    float: right;
                    margin-right: 5px;
                 }
            }
        }
        .isData{
            width: 250px;
            height: 250px;
            position: absolute;
            top: 267px;
            left: 17%;
            background: url(../assets/img/icon_mine_gift_card_empty.png) no-repeat 0px 0px;
            background-size: 100%;
 }
    }
</style>